import React, { Component } from 'react';
import { is, fromJS } from 'immutable';
import './BuySell.scss';
const margin = {
    margin: '10px 0'
};
const borderBottom = {
    borderRight: '1px solid #CFCFCF'
};
export default class BuySell extends Component{
    state = {
        base: {
            coin: "KGT",
            symbol: "1.3.0",
            canUse: 0,
            price: 0,
            amount: 0,
            fee: 0,
            expected: 0
        },
        quote: {
            coin: "BDS",
            symbol: "1.3.1",
            canUse: 0,
            price: 0,
            amount: 0,
            fee: 0,
            expected: 0
        }
    };
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps))|| !is(fromJS(this.state),fromJS(nextState))
    };
    render(){
        const {base,quote} = this.state;
        return(
            <div className="buy_sell">
                <div className="trade">
                    <div className="trade-item" style={borderBottom}>
                        <div className="trade-item-assets">
                            <span>可用: {base.canUse} {base.coin}</span>
                            <span>可买 ≈ {base.canUse} {quote.coin}</span>
                        </div>
                        <div className="trade-item-input">
                            <div className="trade-item-input-price">
                                <input type="text" placeholder="兑入价" />
                                    <span>{base.coin}</span>
                            </div>
                            <div className="trade-item-input-num">
                                <input type="text" placeholder="兑入量" />
                                <span>{quote.coin}</span>
                            </div>
                        </div>
                        <div className="trade-item-assets" style={margin}>
                            <span>预计兑换额 : {base.canUse} {base.coin}</span>
                            <span>预计手续费 : {base.canUse} {base.fee}</span>
                        </div>
                        <button id="buyIn" className="trade-item-submit">兑入</button>
                    </div>
                    <div className="trade-item" style={borderBottom}>
                        <div className="trade-item-assets">
                            <span>可用: {quote.canUse} {quote.coin}</span>
                            <span>可买 ≈ {quote.canUse} {base.coin}</span>
                        </div>
                        <div className="trade-item-input">
                            <div className="trade-item-input-price">
                                <input type="text" placeholder="兑入价" />
                                <span>{base.coin}</span>
                            </div>
                            <div className="trade-item-input-num">
                                <input type="text" placeholder="兑入量" />
                                <span>{quote.coin}</span>
                            </div>
                        </div>
                        <div className="trade-item-assets" style={margin}>
                            <span>预计兑换额 : {quote.canUse} {quote.coin}</span>
                            <span>预计手续费 : {quote.canUse} {quote.fee}</span>
                        </div>
                        <button id="sellOut" className="trade-item-submit">兑出</button>
                    </div>
                </div>
            </div>
        );
    }
}



